import videoobject from "@/components/script/videoobject";

export default {
  name: "video_item",
  mixins: [videoobject],
  props: {
    topic_data: {},
    show_node: undefined,
    show_image: undefined,
    set_top: undefined
  }
  ,
  data: function () {
    return {
      preivew_url: undefined,
      online_video: undefined
    }
  }
  ,
  created() {
    console.log('create video_itme:',this.topic_data);
    this.online_video = this.topic_data.video_info.online_videos[0];
  }
  ,
  computed: {

  }
  ,
  methods: {
    get_preview_url: function () {
      var preview_mp4_url = "https://" + this.$config.space_confs[this.online_video.space_name].static_hosts[0] + "/videos/" + this.online_video.hash_id + "/preview.mp4";
      console.log('preview_mp4_url:', preview_mp4_url);
      return preview_mp4_url;
    }
    ,
    get_cover_image_url: function () {
      var cover_image_url = this.generate_small_cover_url(this.online_video.space_name, this.online_video.hash_id);
      return cover_image_url;
    }
    ,
    on_cover_error: function (e) {
      console.log('[on_cover_error]:', e);
    }
    ,
    mouseleave: function (e) {
      console.log('[mouseleave]:', e);
      this.preivew_url = undefined;
    }
    ,
    mouseover: function (e) {
      this.preivew_url = this.get_preview_url();
      console.log('[mouseover]:', this.preivew_url);

    }
  }
}
