import Vue from "vue";
import './assets/css/app.css'
import './assets/css/extra.css'
import './assets/css/ruby_china.css'
import './assets/css/forum.css'
import './assets/css/dark.theme.css'
import './assets/css/theme.css'
import './assets/mobile/app.css'


export default {
  name: 'App',
  data() {
    return {
      header_show: true,
      footer_show: true,
      header_margin_top: 0
    }
  },
  created() {
    window.app = this;
    Vue.prototype.$app = this;

    //dark 模式
    this.$vuetify.theme.dark = true;
    this.$q.dark.set(true);
    this.$nextTick(() => {
      this.init_header_height();
    });
  },
  methods: {
    init_header_height: function () {
      if (!this.$refs.appheader.header_height || this.$refs.appheader.header_height < 1) {
        return setTimeout(this.init_header_height, 100);
      }
      try {
        console.log('appheader height:', this.$refs.appheader.header_height);
        this.header_margin_top = this.$refs.appheader.header_height + 5;
      } catch (e) {
        this.header_margin_top = 0;
      }

      if (this.header_margin_top == 0) {
        this.header_margin_top = 70;
        if (this.is_mobile()) {
          this.header_margin_top += 30;
        }
      }
      console.log('header_margin_top:', this.header_margin_top);
    },
    //是否显示头部
    header: function (bool) {
      this.header_show = bool;
    },
    //是否显示底部
    footer: function (bool) {
      this.footer_show = bool;
    }
  }
}
