import Vue from "vue";

Vue.prototype.init_traffic = function () {
    var url_vars = this.getUrlVars();
    if (url_vars.f) {
        localStorage.setItem('f', url_vars.f);
    } else {
        var f = localStorage.getItem('f');
        if (!f) {
            f = document.referrer;
            localStorage.setItem('f', f);
        }
    }
}

Vue.prototype.get_from_source = function () {
    var f = localStorage.getItem('f');
    return f;
}
