import forumobjct from "../../script/forumobjct";

export default {
  name: "topic_item",
  mixins: [forumobjct],
  props: {
    topic_data: {},
    show_node: undefined,
    show_image: undefined
  },
  data() {
    return {
      node_conf: undefined

    }
  },
  computed: {
    id: function () {
      if (!this.topic_data.id) {
        return this.topic_data._id;
      }
      return this.topic_data.id;
    },
    show_fanhao: function () {
      try {
        if (this.topic_data.video_info && this.topic_data.video_info.av_data_type == 2) {
          return this.topic_data.video_info.fanhao;
        }
      } catch (e) {
        console.error(e);
      }
      return undefined
    },
    settoessence: function () {
      if (this.topic_data.manager_set) {
        return this.topic_data.manager_set.settoessence;
      }
      return undefined;
    }
  },
  methods: {
    on_goto_topic: function (e) {
      console.log("on_goto_topic:", e);
      if (this.topic_data) {
        this.save_topic_data(this.topic_data);
      }
    }
  },
  created() {
    if (this.show_node) {
      this.node_conf = this.get_node(this.topic_data.fid);
    }
  }
}
