export default {
  name: "adsobject",
  methods: {
    isUrl: function (url) {
      return /^(https?:\/\/(([a-zA-Z0-9]+-?)+[a-zA-Z0-9]+\.)+[a-zA-Z]+)(:\d+)?(\/.*)?(\?.*)?(#.*)?$/.test(url)
    }
    ,
    full_image_url: function (image_url) {
      if (!this.isUrl(image_url)) {
        let new_url = 'https://' + this.$config.ads_image_domains[0] + image_url;
        return new_url;
      }
      console.log('full_image_url:', image_url);
      return image_url;
    }
  }
}
