import forumobjct from "@/components/script/forumobjct";
import videoobject from "@/components/script/videoobject";

export default {
  name: "user___center__star",
  mixins: [forumobjct, videoobject],
  data() {
    return {
      tab: "video",
      video_class_name: '',
      topic_object: {}
    }
  },
  created() {
    this.video_class_name = this.video_col_class_name();
    console.log('video_class_name:', this.video_class_name);

    var fun_object = this.show_video;
    if (location.hash) {
      var fun_name = "show_" + location.hash.substr(1);
      var tmp_fun_object = this[fun_name];
      console.log('type:', typeof tmp_fun_object);
      if (typeof tmp_fun_object == 'function') {
        fun_object = tmp_fun_object;
      }
    }
    fun_object();
    window._t = this;
  },
  methods: {
        show_thread: function () {
      this.tab = 'video';
      this.$axios.get("/app/video/star", {
        params: {
          'start': 0,
          'limit': 30,
          'uid': this.$store.state.user.userinfo.uid,
          'type': 'video'
        }
      }).then((response) => {
        console.log("video star list:", response);
        this.topic_object['video'] = response.data;
        this.$forceUpdate();

      }).catch((error) => {
        console.log("error:", error);
      });
    },
    show_video: function () {
      this.tab = 'video';
      this.$axios.get("/app/video/star", {
        params: {
          'start': 0,
          'limit': 30,
          'uid': this.$store.state.user.userinfo.uid,
          'type': 'video'
        }
      }).then((response) => {
        console.log("video star list:", response);
        this.topic_object['video'] = response.data;
        this.$forceUpdate();

      }).catch((error) => {
        console.log("error:", error);
      });
    },
    on_tab_event: function (e) {
      console.log("on_tab_event:", e);
      var show_method_name = "show_" + e;
      let show_method = this[show_method_name];
      if (show_method) {
        show_method()
      }
    },
  }
}
