import forumobjct from "../../../script/forumobjct";

export default {
  name: "history",
  mixins: [forumobjct],
  props: {
    userinfo: undefined,
  },
  data() {
    return {
      topic_list: undefined,
    }
  },
  created() {
    this.load_topic_view_history({
      start: 0,
      limit: 30,
      cb: this.on_load_history,
      uid: this.userinfo.uid
    });
  },
  methods: {
    on_load_history: function (response) {
      console.log("on_load_history:", response);
      this.topic_list = response.data.topic_list;
    }
  }
}
