import Vue from 'vue'
import Promise from 'es6-promise'
Promise.polyfill()

import './quasar'
import './vuetify'
import vuetify from './plugins/vuetify';

import './modules/app'
import './modules/common'
import './modules/requests'
import './modules/forum'
import './modules/ads'
import './modules/traffic'
import './modules/lang'
import store from './modules/store'
import base from './modules/base'
import './modules/js.extend'
Vue.use(base)

import VueLazyload from 'vue-lazyload'
Vue.use(VueLazyload, {
  preLoad: 1.3,
  attempt: 1
});

import router from './router'
import item_tip from './components/items/tip'
import topic_item from './components/forum/items/topic_item'
import video_item from './components/video/item/video_item'
import header from './components/header'
import footer from './components/footer'

Vue.config.productionTip = false

Vue.component('video_item', video_item)
Vue.component('item_tip', item_tip)
Vue.component('appheader', header)
Vue.component('appfooter', footer)
Vue.component('topic_item', topic_item)

import App from './App.vue'

window.ga = new Vue({
  render: h => h(App),
  vuetify,
  router: router,
  store,
  created() {
    this.$store.dispatch('init');
    this.init()
  }
}).$mount('#app')


