import forumobjct from "@/components/script/forumobjct";

export default {
  name: "actress",
  mixins: [forumobjct],
  data() {
    return {
      name: undefined,
      topic_data_object: undefined
    }
  },
  created() {
    this.name = this.$route.params.name;
    console.log('createactress :', this.name);
    this.set_title(this.name);
    this.add_keywords(this.name);
    this.add_description(this.name);
    this.load_av_datas();
  },
  methods: {
    load_av_datas: function () {
      this.get_topic_list({
        start: 0,
        limit: this._g.PER_PAGE_COUNT,
        from_cache: this.from_cache(),
        video_cast: this.name,
        cb: this.on_load_topic
      });
    },
    on_load_topic: function (e) {
      console.log('on_load_topic:', e);
      this.topic_data_object = e.data;
    }
  }
}
