import topic_edit_item from '@/components/forum/items/topic_edit_item'

export default {
  name: "thread",
  components: {topic_edit_item},
  data() {
    return {
      tab: "released",
      topic_object: {}
    }
  },
  created() {
    var fun_object = this.show_released;
    if (location.hash) {
      var fun_name = "show_" + location.hash.substr(1);
      var tmp_fun_object = this[fun_name];
      console.log('type:', typeof tmp_fun_object);
      if (typeof tmp_fun_object == 'function') {
        fun_object = tmp_fun_object;
      }
    }
    fun_object();
    window._t = this;
  },
  methods: {
    on_tab_event: function (e) {
      console.log("on_tab_event:", e);
      var show_method_name = "show_" + e;
      let show_method = this[show_method_name];
      if (show_method) {
        show_method()
      }
    },
    show_released: function () {
      this.tab = 'released';
      this.$axios.get("/app/topic/list", {
        params: {
          'start': 0,
          'limit': 30,
          'uid': this.$store.state.user.userinfo.uid,
          'type': 'released'
        }
      }).then((response) => {
        console.log("topic list:", response);
        this.topic_object['released'] = response.data;
        console.log("topic_object:", this.topic_object);
        this.$forceUpdate();

      }).catch((error) => {
        console.log("error:", error);
      });
    },
    show_verifying: function () {
      this.tab = 'verifying';
      this.$axios.get("/app/topic/list", {
        params: {
          'start': 0,
          'limit': 30,
          'uid': this.$store.state.user.userinfo.uid,
          'type': 'verifying'
        }
      }).then((response) => {
        console.log("topic list:", response);
        this.topic_object['verifying'] = response.data;
        console.log("topic_object:", this.topic_object);
        this.$forceUpdate();
      }).catch((error) => {

      });
    }
  }
}
