export default {
  name: "create",
  data: function () {
    return {
      node_data: {
        setting: {
          not_reply: false
        }
      }
    }
  },
  created() {
    if (!this.$store.state.hasLogin) {
      this.$q.dialog({
        title: this.$l('title_board_message'),
        message: '对不起，请先登录',
        cancel: false,
        ok: this.$l('login'),
        persistent: true
      }).onOk(data => {
        this.$router.push('/user/login');
      })
    }
  },
  methods: {}
}
