import Vue from 'vue'
import info from './items/info'
import gold from './items/gold'
import star from './items/star'
import history from './items/history'
import buy from './items/buy'
import topic from './items/topic'


import setting from './items/setting'
import affiliate from './items/affiliate'
import userhead from './items/head'

Vue.component('userhead', userhead)

import '../../../assets/css/user.css'
import userobject from "@/components/script/userobject";

export default {
  name: 'usercentr',
  mixins: [userobject],
  components: {
    info,
    gold,
    setting,
    star,
    history,
    buy,
    topic,
    affiliate
  },
  data() {
    return {
      show_what: 'info',
      user: {},
      visit_uid: undefined,
      this_userdata: undefined
    }
  },
  watch: {
    '$route'(to, from) {
      console.log("to:", to, "from:", from);
      console.log('this.$route.params.id:', this.$route.params.id);
      this.show_what = this.$route.params.id;
    }
  },
  computed: {
    visit_self: function () {
      if (this.$store.state.hasLogin) {
        if (this.visit_uid = this.$store.state.user.userinfo.uid) {
          return true;
        }
      }
      return false;
    }
  },
  created() {
    console.log('user ceneter created:', this.$route.params.id);
    window._t = this;
    if (!this.$store.state.hasLogin) {
      //return this.$app._userbar.show_login_dialog();
    }
    console.log('this.$route.query.id:', this.$route.query.id);
    console.log('this.$route.params.id:', this.$route.params.id);

    if ('5eef233beb94dc31801bd9aa'.length == this.$route.params.id.length) {
      this.visit_uid = this.$route.params.id;
      this.get_userdata({
        uid: this.visit_uid, cb: (response) => {
          this.this_userdata = response.data;
          console.log('this.this_userdata:', this.this_userdata);
        }
      });
    } else {
      this.show_what = this.$route.params.id;
      this.this_userdata = this.$store.state.user.userinfo;
    }
    this.listener_login = this.$hub.on('event:login', (data) => {
      this.$forceUpdate();
    });
  },
  destroyed() {
    this.listener_login.off();
  },
  methods: {
    on_submit: function () {

    }
  }

}
