router.beforeEach((to, from, next) => {

    /* 路由发生变化修改页面meta */
    if (to.meta.content) {
        let head = document.getElementsByTagName('head');
        let meta = document.createElement('meta');

        /*Vue.prototype.add_keywords(Vue.prototype.langloader('seo.keywords'));
        Vue.prototype.add_keywords(to.meta.content.keywords);

        Vue.prototype.add_description(Vue.prototype.langloader('seo.description'));
        Vue.prototype.add_description(to.meta.content.description);*/

        meta.content = to.meta.content;
        head[0].appendChild(meta)
    }
    // /* 路由发生变化修改页面title */
    if (to.meta.title) {
        Vue.prototype.set_title(to.meta.title);
        if (to.meta.content) {
            Vue.prototype.set_keywords(to.meta.content.keywords);
            Vue.prototype.set_description(to.meta.content.description);
        }
    }
    next()
});


Vue.prototype.set_keywords = function (keywords) {
    document.querySelector('meta[name="keywords"]').setAttribute('content', keywords);
}

Vue.prototype.add_keywords = function (keywords) {
    var orginal_keywords = document.querySelector('meta[name="keywords"]').getAttribute('content');
    if (orginal_keywords && orginal_keywords.length > 0) {
        orginal_keywords += "," + keywords;
    } else {
        orginal_keywords = keywords;
    }
    document.querySelector('meta[name="keywords"]').setAttribute('content', orginal_keywords);
}

Vue.prototype.set_description = function (description) {
    document.querySelector('meta[name="description"]').setAttribute('content', description);
}

Vue.prototype.add_description = function (description) {
    var orginal_description = document.querySelector('meta[name="description"]').getAttribute('content');
    if (orginal_description && orginal_description.length > 0) {
        orginal_description += "," + description;
    } else {
        orginal_description = description;
    }
    document.querySelector('meta[name="description"]').setAttribute('content', orginal_description);
}


